<template>
	<section id="signin" class=" d-flex align-items-center">

    <div class="container">
      <div class="row">
        <div class="col-lg-7 d-flex flex-column justify-content-center">
          <div class="signin-lgo">
         <img src="../assets/images/workwire.png">
       </div>


        </div>

           <div class="col-lg-5 d-flex  justify-content-center align-items-center pt-4 ">

           <div class="signin-box_b">
