                 <div class="form-group">
            <input type="text" name="" class="form-control" placeholder="Email adresse">
             </div>
            </form>

            <div class="box-join_b">
            <a href="#" class="join-signin_b"> Continue <img src="../assets/images/Group.png"> </a>
         </div>

         <div class="or-line">
          <p> OR</p>

         </div>
   
</div>
<div class="all-social">
  <div>
<div>
<a href="#" class="alradt-acount_b"> <img src="../assets/images/Facebook-Logo.png"> <label> Continue with Facebook </label>  </a>
</div>
<div>
<a href="#" class="alradt-acount_c"> <img src="../assets/images/Google-Logo.png"> <label> Continue with Facebook </label>  </a>
</div>
<div>
<a href="#" class="alradt-acount_d"> <img src="../assets/images/Apple-Logo.png"> <label> Continue with Facebook </label>  </a>
