<script>
import {
    loginRequest
} from './ApiFunctions/index';

export default {
  name: "SigninForm",
  data() {
        return {
            errorMessage: '',
            post: {
                email: '',
                password: ''
            }
        }
    },
    methods: {
        async LoginUser() {
            const res = await loginRequest('/login-user', this.post)
            if (res.auth) {
                this.$router.push({
                    name: 'Home',
                    path: ''
                })
            } else if (res.error) {
                this.errorMessage = res.error;
            } else {
                this.errorMessage = '';
                this.$router.push({
                    name: 'SignupForm',
                    path: '/sign-up'
                })

            }

        }
    }
}
</script>


